import React, {useState} from 'react'
import styled from 'styled-components'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import Footer from '../../components/Footer/Footer'
import BreadCrumbs from '../components/Breadcrumbs'
import NavbarBottom from '../components/NavbarBottom'
import Sidebar from '../components/Publicite/Sidebar'
import { Container,makeStyles, Grid, Typography, Box } from '@material-ui/core'
import {dataAnnonces} from '../components/data/dataAnnonces'
import CardAnnonces from '../components/Annonces/CardAnnonces'
import {jobData }from '../components/data/jobData'
import ViewJobModal from '../components/Job/ViewJobModal'
import JobCard from '../components/Job/JobCard'
import NavbarSearch from '../components/NavbarSearch'
import CardPub from '../components/Publicite/CardPub'





const HeadSection = styled.div`
   position:fixed;
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`
const PageContent = styled.div`   
   padding-top: 225px;
   margin-left:30px;
   margin-bottom: 20px;

   @media screen and (max-width: 768px){
       margin-left:30px;
    }
  
`
const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 2,
     
    }
  }));

const OffreEmploi = () => {

    const [viewJob, setViewJob] = useState(false)

    const classes = useStyles();

    const [isOpen, setIsOpen] = useState(false)

    const toggle = () =>{
        setIsOpen(!isOpen)
    }
    return (
        <>
            <ScrollToTop/>
            <HeadSection>
                <Sidebar  isOpen={isOpen}  toggle={toggle}/>
                <NavbarSearch/>
               {/* <PubLaterale   toggle={toggle}/>*/}
                <NavbarBottom/>
                <BreadCrumbs/>
            </HeadSection>

            <PageContent className={classes.root}>
                    <Container >
                            <Grid container spacing={3} lg={12} md={12}  xs={12}>


                                    <Grid item   lg={8} md={6} xs={12}>
                                        <Box display="flex" mb={1}>
                                           <Typography variant="h6" component="h2">Offres d'emploi:  6 résultats</Typography>
                                          
                                        </Box>
                                        <ViewJobModal  job={viewJob}  closeModal={()=> setViewJob({})}/>
                                        {jobData.map(job => 
                                                <JobCard  open={()=>setViewJob(job)} key={job.id} {...job} />                
                                            )
                                        }
                                    </Grid>
                                      
                                        {dataAnnonces.splice(0,1).map(annonce => 
                                         
                                            <CardAnnonces  key={annonce.id} {...annonce}  />

                                        )}

                                  
                            </Grid>
                    </Container>
            </PageContent>
            <Footer/>
            
        </>
    )
}

export default OffreEmploi
